import { React, useState, useEffect } from "react";
import { Link } from "react-router-dom";

import { postRecipe, getTypeDiet } from "../Redux/Actions";
import { useDispatch, useSelector } from "react-redux";
import styles from "./RecipeCreate.module.css";

function validate(input) {
  let errors = {};
  if (!input.title) {
    errors.title = "Title is require";
  } else if (!input.summary) {
    errors.summary = "Summary is require";
  } else if (!input.dishTypes) {
    errors.dishTypes = "A dish type is require";
  } else if (!input.healthScore) {
    errors.dishTypes = "Health score is require";
  } else if (!input.analyzedInstructions) {
    errors.dishTypes = "Steps are require";
  }

  return errors;
}

export default function RecipeCreate() {
  const stateInitialForms = {
    title: "",
    dishTypes: "",
    summary: "",
    healthScore: "",
    analyzedInstructions: "",
    image: "",
    diets: [],
  };

  const [input, setInput] = useState(stateInitialForms);
  const [errors, setErrors] = useState({});

  function handleDelete(e) {
    e.preventDefault();
    setInput({
      ...input,
      diets: input.diets.filter((t) => t !== e),
    });
  }

  const dispatch = useDispatch();
  const diets = useSelector((state) => state.diets);

  useEffect(() => {
    dispatch(getTypeDiet());
  }, [dispatch]);

  const handleInputChange = (e) => {
    setInput({
      ...input,

      [e.target.name]: e.target.value,
    });

    setErrors(
      validate({
        ...input,
        [e.target.name]: e.target.value,
      })
    );
  };

  const handleSelect = (e) => {
    setInput({
      ...input,
      diets: [...input.diets, e.target.value],
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if(!Object.keys(errors).length && input.name.length){
        dispatch(postRecipe(input));
        alert("Recipe Created");
        setInput({
          title: "",
          dishTypes: "",
          summary: "",
          image: "",
          healthScore: "",
          analyzedInstructions: "",
          diets: [],
        });

    } else {
        alert("Error")
    }

  };

  return (
    <div className={styles.container}>
      <Link to="/home">
        <button className={styles.btn}>Back</button>
      </Link>

      <form className={styles.form} onSubmit={(e) => handleSubmit(e)}>
        <label className={styles.label}>Title:</label>
        <input
          className={styles.input}
          type="text"
          name="title"
          value={input.title}
          onChange={(e) => handleInputChange(e)}
        />
        {errors.title && <p className={styles.errors}>{errors.title}</p>}

        <label className={styles.label}>Summary:</label>
        <input
          className={styles.input}
          type="text"
          name="summary"
          value={input.summary}
          onChange={(e) => handleInputChange(e)}
        />
        {errors.summary && <p className={styles.errors}>{errors.summary}</p>}

        <label className={styles.label}>Dish Type:</label>
        <input
          className={styles.input}
          type="text"
          name="dishTypes"
          value={input.dishTypes}
          onChange={(e) => handleInputChange(e)}
        />
        {errors.dishTypes && (
          <p className={styles.errors}>{errors.dishTypes}</p>
        )}

        <label className={styles.label}>Health Score:</label>
        <input
          className={styles.input}
          type="number"
          name="healthScore"
          min="10"
          max="100"
          value={input.healthScore}
          onChange={(e) => handleInputChange(e)}
        />
        {errors.healthScore && (
          <p className={styles.errors}>{errors.healthScore}</p>
        )}

        <label className={styles.label}>Steps:</label>
        <input
          className={styles.input}
          type="text"
          name="analyzedInstructions"
          value={input.analyzedInstructions}
          onChange={(e) => handleInputChange(e)}
        />
        {errors.analyzedInstructions && (
          <p className={styles.errors}>{errors.analyzedInstructions}</p>
        )}

        <label className={styles.label}>Image:</label>
        <input
          className={styles.input}
          type="text"
          name="image"
          value={input.image}
          onChange={(e) => handleInputChange(e)}
        />

        <select className={styles.input} onChange={(e) => handleSelect(e)}>
          {diets.map((dl ) => (
              
                  <option  value={dl.name}>{dl.name}</option>
              
            
          ))}
        </select>

        {/* <div>
                   <label>Diets: </label>
                   <label><input type = 'checkbox' name= 'gluten free' value= "gluten free"></input>Glulen Free</label>
                   <label><input type = 'checkbox' name = 'lacto-ovo-vegetarian' value = 'lacto-ovo-vegetarian'></input>Lacto ovo vegetarian</label>
                   <label><input type = 'checkbox' name = 'vegan' value = 'vegan'></input>Vegan</label>
                   <label><input type = 'checkbox' name = 'pescatarian' value = 'pescatarian'></input>Pescatarian</label>
                   <label><input type = 'checkbox' name = 'paleolithic' value = 'paleolithic'></input>Paleolithic</label>
                   <label><input type = 'checkbox' name = 'primal' value = 'primal'></input>Primal</label>
                   <label><input type = 'checkbox' name = 'whole 30' value = 'whole 30'></input>Paleolithic</label>
                   <label><input type = 'checkbox' name = 'dairy free' value = 'dairy free'></input>Dairy Free</label>
               </div> */}

        <button className={styles.title} type="submit">Create Recipe</button>
      </form>

      {/* {input.diets.map((d) => (
        <div >
          <p>{d}</p>
          <button onClick={() => handleDelete(d)}>x</button>
        </div>
      ))} */}

      <h1 className={styles.title}>Create Recepe</h1>
    </div>
  );
}
